import { Flashcards } from "components/Flashcards/Flashcards";

import { MediaStreamProvider } from "contexts/MediaStreamContext";
import { AudioAnalyserProvider } from "contexts/AudioAnalyserContext";
import { InputAudioProvider } from "contexts/InputAudioContext";
import { useEffect } from "react";
import { PitchDetectionProvider } from "contexts/PitchDetectionContext";

import { useMediaStream } from "contexts/MediaStreamContext";

export const GamePage = () => {
  return (
    <MediaStreamProvider video={false} audio={true}>
      <InputAudioProvider>
        <AudioAnalyserProvider>
          <PitchDetectionProvider>
            <Page />
          </PitchDetectionProvider>
        </AudioAnalyserProvider>
      </InputAudioProvider>
    </MediaStreamProvider>
  );
};

const Page = () => {
  const { stream, start, stop } = useMediaStream();

  useEffect(() => {
    if (stream) return;

    return function cleanup() {
      if (stream) stop();
    };
  }, [stream, start, stop]);

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {!stream && (
        <button
          onClick={() => {
            start();
          }}
        >
          Start
        </button>
      )}

      {stream && <Flashcards />}
    </div>
  );
};
