import {
  createContext,
  useContext,
  FunctionComponent,
  useEffect,
  useState,
} from "react";
import { note, Note, NoNote } from "@tonaljs/core";

import { useAudioAnalyser } from "contexts/AudioAnalyserContext";

import { PitchDetector } from "pitchy";
import { useInputAudio } from "contexts/InputAudioContext";
const FrequencyMap = require("note-frequency-map");

interface PitchDetectionValue {
  centsOff: number | undefined | null;
  frequency: number | undefined | null;
  name: string | undefined | null;
  note: Note | NoNote;
  octave: number | undefined | null;
  clarity: number | undefined | null;
  tuning: any;
}

const PitchDetectionContext = createContext<PitchDetectionValue>({
  centsOff: undefined,
  frequency: undefined,
  name: undefined,
  note: note("Nonote"),
  octave: undefined,
  tuning: undefined,
  clarity: undefined,
});

export const usePitchDetection = () => useContext(PitchDetectionContext);

export const PitchDetectionProvider: FunctionComponent = ({ children }) => {
  const { analyser } = useAudioAnalyser();
  const { audioCtx } = useInputAudio();

  const [pitch, setPitch] = useState<number | null>(null);
  const [clarity, setClarity] = useState<number | null>(null);

  useEffect(() => {
    if (!analyser || !audioCtx) return;

    const data = new Float32Array(analyser.fftSize);
    const detector = PitchDetector.forFloat32Array(data.length);

    const update = () => {
      analyser.getFloatTimeDomainData(data);
      const [currentPitch, currentClarity] = detector.findPitch(
        data,
        audioCtx.sampleRate
      );
      setPitch(currentPitch);
      setClarity(currentClarity);
    };

    const handle = setInterval(update, 100);
    return () => clearInterval(handle);
  }, [analyser, audioCtx]);

  let result: PitchDetectionValue = {
    centsOff: null,
    frequency: null,
    name: null,
    note: note("Nonote"),
    octave: null,
    tuning: null,
    clarity: null,
  };

  if (clarity && pitch) {
    const data = FrequencyMap.noteFromFreq(pitch);

    result = {
      ...data,
      clarity,
      note: note(data.note),
    };
  }

  return (
    <PitchDetectionContext.Provider value={result}>
      {children}
    </PitchDetectionContext.Provider>
  );
};

export default PitchDetectionContext;
