import { useEffect, useState } from "react";
import { note as getNote } from "@tonaljs/core";

import { useCountdown } from "hooks/useCountdown";
import { NoteAudioPlayer } from "components/NoteAudioPlayer/NoteAudioPlayer";
import { MusicNote } from "components/MusicNote/MusicNote";
import { NoteCard } from "components/NoteCard/NoteCard";

import { beginnerNotes, isNote } from "helpers/notes";
import { usePitchDetection } from "contexts/PitchDetectionContext";

export const Flashcards = () => {
  const [points, setPoints] = useState(0);
  const [showAll, setShowAll] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [cardType, setCardType] = useState("");
  const { count, reset: resetCountdown } = useCountdown(5);

  const { note: noteDetected, clarity: noteClarity } = usePitchDetection();

  const [cardsData] = useState(beginnerNotes.map((note) => getNote(note)));
  const numberOfCards = Math.max(Math.round(points / 10), 3);
  const getNewCard = () => {
    let newIndex = Math.floor(Math.random() * numberOfCards);
    if (newIndex === currentIndex) newIndex--;
    if (newIndex < 0) newIndex = 1;

    setCurrentIndex(newIndex);
    setCardType(shuffleArray(["sheetMusic", "sound", "name"]).pop());
    setShowAll(false);
    resetCountdown();
  };

  useEffect(() => {
    getNewCard();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (count === 0) {
      setShowAll(true);
    }
  }, [count]);

  const card = cardsData[currentIndex];

  if (((noteDetected.name === card.name && noteClarity) || 0) > 0.8) {
    getNewCard();

    if (count > 0) setPoints(points + 1);
    else setPoints(Math.max(points - 1, 0));
  }

  if (!isNote(card)) return <>Something went wrong...</>;

  return (
    <div>
      {!showAll && (
        <div>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              marginBottom: 10,
              borderBottom: "1px solid #888",
              paddingBottom: 5,
            }}
          >
            <span>{count}⌛</span>
            <span>{numberOfCards}🃏</span>
            <span>{points}⚡</span>
          </div>
          {cardType === "name" && <h1>{card.name}</h1>}
          {cardType === "sound" && <NoteAudioPlayer autoplay note={card} />}
          {cardType === "sheetMusic" && <MusicNote note={card} />}
          <button
            onClick={() => {
              setShowAll(true);
            }}
          >
            Reveal
          </button>
        </div>
      )}

      {showAll && (
        <div>
          <NoteCard note={card} />
          <button
            onClick={() => {
              getNewCard();
            }}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};

function shuffleArray(array: any) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
}
