import { useEffect } from "react";

import { MediaStreamProvider } from "contexts/MediaStreamContext";
import { AudioAnalyserProvider } from "contexts/AudioAnalyserContext";
import { InputAudioProvider } from "contexts/InputAudioContext";
import { useMediaStream } from "contexts/MediaStreamContext";
import {
  PitchDetectionProvider,
  usePitchDetection,
} from "contexts/PitchDetectionContext";

import { Tab } from "components/Tab/Tab";
import AudioVisualizer from "components/AudioVisualizer/AudioVisualizer";

import { MusicNote } from "components/MusicNote/MusicNote";

export const TunerPage = () => {
  return (
    <MediaStreamProvider video={false} audio={true}>
      <InputAudioProvider>
        <AudioAnalyserProvider>
          <PitchDetectionProvider>
            <Page />
          </PitchDetectionProvider>
        </AudioAnalyserProvider>
      </InputAudioProvider>
    </MediaStreamProvider>
  );
};

const Page = () => {
  const { stream, start, stop } = useMediaStream();
  const { centsOff, frequency, clarity, note } = usePitchDetection();

  useEffect(() => {
    if (stream) return;

    start();

    return function cleanup() {
      if (stream) stop();
    };
  }, [stream, start, stop]);

  return (
    <div>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          width: "95vw",
        }}
      >
        <div style={{ width: 100 }}>
          <h1>{note.name}</h1>
          <h4>￠: {centsOff ? round(centsOff) : ""}</h4>
        </div>
        <div>
          <AudioVisualizer style={{ width: 60, height: 60 }} />
        </div>
        <div style={{ textAlign: "right", width: 140 }}>
          <h1>{frequency && round(frequency) + " ㎐"}</h1>
          <h4>clarity: {clarity ? round(clarity * 100) : ""}</h4>
        </div>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "95vw",
        }}
      >
        <div
          style={{
            height: 30 * 7,
            width: 30,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {!note.empty && (
            <Tab note={note} style={{ height: 30 * 7, width: 30 }} />
          )}
        </div>
        <div style={{ width: 120 }}>
          {!note.empty && (clarity || 0) > 0.8 && (note.oct || 0) > 2 && (
            <MusicNote note={note} />
          )}
        </div>
      </div>
    </div>
  );
};

const round = (val: number) => {
  return Math.round((val * 10) / 10);
};
