import React, { useEffect, useState, useRef } from "react";
import abcjs from "abcjs";

export const Abc = ({
  abc,
  params,
}: {
  abc: string;
  params?: abcjs.AbcVisualParams;
}) => {
  const [abcTune, setAbcTune] = useState(abc);
  const inputEl = useRef(null);

  useEffect(() => {
    inputEl?.current && abcjs.renderAbc(inputEl.current, abcTune, params);
  }, [abcTune, params]);

  useEffect(() => {
    setAbcTune(abc);
  }, [abc]);

  return (
    <div>
      <div ref={inputEl}></div>
    </div>
  );
};
